* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

:root {
    --primary-color: #6667ab;
}

.container {
    max-width: 1230px;
    margin: 0 auto;
    padding: 0 15px;
}

.App>.container {
    display: flex;
}

.fa_exclamation {
    font-size: 15rem;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0.1em 0;
}

.fa_exclamation svg {
    color: #cd0000;
}

.fa_exclamation p {
    font-size: 2rem;
    font-family: 'Bahnschrift';
}